/*Checkboxes and radios*/
input[type="checkbox"],
input[type="radio"] {
  opacity: 0;
  position: absolute;
}

input[type="checkbox"] + label,
input[type="radio"] + label {
  display: block;
  cursor: pointer;
  position: relative;
  padding-left: 25px;
  font-weight: 400;
}

input[type="checkbox"] + label:before,
input[type="radio"] + label:before {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  background-color: #fff;
  border: 1px solid var(--thm-border);
  border-radius: 3px;
  display: inline-block;
  -ms-flex-negative: 0;
  flex-shrink: 0;
  height: 16px;
  outline: 0;
  -webkit-transition: background-color 0.2s ease-out;
  -o-transition: background-color 0.2s ease-out;
  transition: background-color 0.2s ease-out;
  width: 16px;
}

input[type="radio"] + label:before {
  border-radius: 50%;
}

input[type="checkbox"] + label:after,
input[type="radio"] + label:after {
  position: absolute;
  top: 50%;
  left: 3px;
  width: 10px;
  height: 5px;
  content: " ";
  transform: translateY(-50%) rotate(-45deg);
  border-bottom: 2px solid #fff;
  border-left: 2px solid #fff;
  opacity: 0;
  transition: 0.3s;
}

input[type="radio"] + label:after {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  border: 0;
  background-color: var(--thm-base);
}

input[type="checkbox"]:checked + label:before,
input[type="checkbox"]:checked + label.switch-toggle:before {
  background-color: var(--thm-base);
  border-color: var(--thm-base);
}

input[type="checkbox"]:checked + label:after,
input[type="radio"]:checked + label:after {
  opacity: 1;
}

.custom-control {
  padding-left: 0;
}
.custom-radio .custom-control-input:checked ~ .custom-control-label::before,
.custom-checkbox .custom-control-input:checked ~ .custom-control-label::before {
  background-color: var(--thm-base);
}
.custom-control-input:focus ~ .custom-control-label::before {
  -webkit-box-shadow: none;
  box-shadow: none;
}

/*Checkbox With Toggle Swich*/
input[type="checkbox"] + label.switch-toggle {
  padding-left: 70px;
}

input[type="checkbox"] + label.switch-toggle:before {
  border-radius: 16px;
  height: 30px;
  width: 60px;
  background-color: var(--thm-border);
}

input[type="checkbox"] + label.switch-toggle:after {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  border: 0;
  background-color: #fff;
  opacity: 1;
}

input[type="checkbox"]:checked + label.switch-toggle:after {
  left: 36px;
}

