}

.slick-dots {
  display: flex;
  align-items: center;
  justify-content: center;
}

[class*="btn-style-"] .slick-prev::after,
[class*="btn-style-"] .slick-next::after {
  display: none;
}

[class*="btn-style-"] .slick-prev::before,
[class*="btn-style-"] .slick-next::before {
  content: "\f053";
  position: absolute;
  top: 50%;
  left: 50%;
  right: auto;
  bottom: auto;
  transform: translate(-50%, -50%);
  width: auto;
  height: auto;
  opacity: 1;
  visibility: visible;
  background-color: transparent;
}

[class*="btn-style-"] .slick-prev:hover::before,
[class*="btn-style-"] .slick-next:hover::before {
  width: auto;
  height: auto;
  top: 50%;
  left: 50%;
  right: auto;
  bottom: auto;
  transform: translate(-50%, -50%);
}

[class*="btn-style-"] .slick-next::before,
.slick-next:before {
  content: "\f054";
}

.slick-dots li:focus,
.slick-dots li:hover,
.slick-dots li button:focus,
.slick-dots li button:hover {
  outline: none;
}

