}

.sigma_info.style-22 .sigma_info-description h5 {
  margin: 0 0 14px;
}

/* info box style 23 */

.sigma_info-wrapper.style-23 {
  position: relative;
  padding: 0;
  background-color: transparent;
}

.sigma_info-wrapper.style-23:after {
  content: "";
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: 44px;
  width: calc(100% - 30px);
  height: 1px;
  z-index: -1;
  background-color: var(--thm-border);
}

.sigma_info.style-23 {
  padding: 0;
  box-shadow: none;
}

.sigma_info.style-23 .sigma_info-title {
  margin-bottom: 0;
  align-items: flex-start;
}

.sigma_info.style-23 .sigma_info-description h5 {
  margin-bottom: 5px;
}
.sigma_info.style-23 .sigma_info-title .sigma_info-icon {
  width: 110px;
  height: 110px;
  background-color: #fff;
  border-radius: 6px;
  box-shadow: 0 0 10px 5px rgba(0, 0, 0, 0.03);
  color: var(--thm-base);
  margin-right: 28px;
}
.sigma_info.style-23 .sigma_info-title .sigma_info-icon i {
  font-size: 56px;
}

.sigma_info.style-23 .sigma_info-description .steps {
  font-size: 18px;
  display: block;
  font-weight: 600;
  color: var(--thm-base);
  margin: 10px 0 18px;
}

.sigma_info.style-23 .sigma_info-description .pulsive-dot {
  position: relative;
  display: inline-block;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background-color: var(--thm-base);
  margin-left: 6px;
}
.sigma_info.style-23 .sigma_info-description .pulsive-dot:before {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  background-color: var(--thm-base);
  opacity: 0.2;
  width: calc(100% + 16px);
  height: calc(100% + 16px);
  border-radius: 50%;
}

/* info box style 24 */

.sigma_info-wrapper.style-24 {
  background-color: #fff;
  padding: 20px 50px;
