}

.sigma_info.style-25 .sigma_info-description {
  flex: 1;
}

.sigma_info.style-25 .sigma_info-description h5 {
  margin-bottom: 10px;
}

.sigma_info.style-25 .sigma_info-description h5 a:hover {
  color: var(--thm-base-hover);
}

/*-------------- Category --------------------------- */
/* Category style 1 */
.sigma_category {
  position: relative;
  overflow: hidden;
  margin-bottom: 30px;
}

.sigma_category a,
.sigma_category img {
  width: 100%;
  transition: 0.3s;
}

.sigma_category img {
  height: 100%;
  object-fit: cover;
}

.sigma_category:hover img {
  transform: scale(1.1);
}

.sigma_category .sigma_category-content {
  width: calc(100% - 20px);
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: 10px;
}

.sigma_category .sigma_category-content span {
  background-color: #fff;
  padding: 12px 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--thm-secondary);
  font-size: 15px;
  font-weight: 700;
  border-radius: 0;
  box-shadow: 0 0 40px 5px rgba(0, 0, 0, 0.03);
}

/* Category style 2 */
.sigma_category.style-2 .sigma_category-content {
  width: auto;
  left: 30px;
  top: 30px;
  bottom: auto;
  transform: none;
}

.sigma_category.style-2 .sigma_category-content span {
  background-color: #fff;
  padding: 8px 15px;
  border-radius: 0;
}

/* Category style 3 */
.sigma_category.style-3 {
  transition: 0.3s;
}

.sigma_category.style-3 .sigma_category-content {
  text-align: center;
  color: var(--thm-secondary);
  margin-bottom: 10px;
}

.sigma_category.style-3 .sigma_category-content h6 {
  margin: 0;
  font-size: 24px;
  transition-duration: 0.3s;
  transition-delay: 0.1s;
  transform: translateY(20px);
}

.sigma_category.style-3 .sigma_category-content span {
  background-color: transparent;
  padding: 0;
  color: var(--thm-b-text);
  opacity: 0;
  visibility: hidden;
  transform: translateY(20px);
  transition-duration: 0.3s;
  transition-delay: 0.1s;
  font-weight: 400;
  box-shadow: none;
}

.sigma_category.style-3:hover {
  transform: scale(1.3);
  box-shadow: 0 0 40px 5px rgba(0, 0, 0, 0.1);
}

.sigma_category.style-3:hover .sigma_category-content h6,
.sigma_category.style-3:hover .sigma_category-content span {
  transform: translateY(0);
}

.sigma_category.style-3:hover .sigma_category-content h6 {
  font-size: 20px;
}

.sigma_category.style-3:hover .sigma_category-content span {
  opacity: 1;
  visibility: visible;
  font-size: 12px;
}

/*-------------- General Section --------------------------- */
/* Style 1 */
.sigma_general-list {
  position: relative;
  margin-bottom: 30px;
}

.sigma_general-list ul li {
  display: flex;
  align-items: center;
  background-color: #fff;
  border-radius: 30px;
  padding: 10px;
}

.sigma_general-list ul li + li {
  margin-top: 20px;
}

.sigma_general-list ul li i {
  width: 40px;
  height: 40px;
  background-color: var(--thm-base);
  color: #fff;
  border-radius: 50%;
  font-size: 18px;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 3px 8px rgba(var(--thm-base-rgb), 0.5);
}

.sigma_general-list ul li span {
  display: inline-block;
  font-size: 16px;
  font-weight: 500;
  color: var(--thm-secondary);
  padding-left: 20px;
  flex: 1;
}

/* Style 2 */
.sigma_general-list.style-2 ul li,
.sigma_general-list.style-3 ul li {
  background-color: transparent;
  padding: 0;
  border-radius: 0;
}

/* Style 3 */
.sigma_general-list.style-4 ul li i,
.sigma_general-list.style-3 ul li i {
  background-color: transparent;
  color: var(--thm-base);
  box-shadow: none;
  width: auto;
  height: auto;
}

/* Style 4 */
.sigma_general-list.style-4 ul li {
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.sigma_general-list.style-4 ul li + li {
  margin-top: 15px;
}

/* Style 5 */
.sigma_general-list.style-5 ul li {
  display: block;
  padding: 0;
  margin: 0;
  border-radius: 0;
  background-color: transparent;
}
.sigma_general-list.style-5,
.sigma_general-list.style-5 ul li p {
  margin: 0;
}
.sigma_general-list.style-5 ul li span {
  display: block;
  padding: 0;
}

.sigma_general-list.style-5 ul li + li {
  margin-top: 15px;
  padding-top: 15px;
  border-top: 1px solid var(--thm-border);
}

/*-------------- Blog Sections --------------------------- */
/* Pagination */
.pagination {
  margin-top: 60px;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  margin-bottom: 0;
}

.pagination .page-item {
  margin: 0;
}

.pagination .page-item:last-child .page-link,
.pagination .page-item:first-child .page-link,
.pagination .page-item .page-link {
  border-radius: 50%;
}

.pagination .page-link {
  color: var(--thm-secondary);
  border: 0;
  background-color: #fff;
  border: 1px solid var(--thm-border);
  border-radius: 8px;
  font-size: 14px;
  font-weight: 600;
  width: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 50px;
  margin-left: 15px;
}

.pagination .page-item.active .page-link {
  background-color: var(--thm-base);
  border: 1px solid var(--thm-base);
}

.pagination .page-item:not(.active) .page-link:hover,
.pagination .page-item:not(.active) .page-link:focus {
  color: #fff;
  background-color: var(--thm-base);
  border-color: var(--thm-base);
  outline: none;
  box-shadow: none;
}

/* Style 1 */
.sigma_post {
  position: relative;
  box-shadow: 0 0 40px 5px rgba(0, 0, 0, 0.03);
  background-color: #fff;
  margin-bottom: 30px;
  transition: 0.3s;
}

.sigma_post .sigma_post-thumb {
  position: relative;
  overflow: hidden;
}

.sigma_post .sigma_post-thumb a:first-child {
  display: block;
}

.sigma_post .sigma_post-thumb img {
  transition: 0.3s;
  width: 100%;
}

.sigma_post .sigma_post-thumb:hover img {
  transform: scale(1.1);
}

.sigma_post .sigma_post-categories {
  position: absolute;
  left: 20px;
  top: 20px;
  display: flex;
  align-items: center;
}

.sigma_post .sigma_post-categories a,
.widget.widget-sigma-recent-posts.style-3
  .sigma_recent-post
  .sigma_post-categories
  a {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 6px 15px;
  background-color: var(--thm-base);
  color: #fff;
  font-weight: 500;
  font-size: 14px;
  border-radius: 26px;
  transition: 0.3s;
}

.sigma_post .sigma_post-categories a:hover,
.widget.widget-sigma-recent-posts.style-3
  .sigma_recent-post
  .sigma_post-categories
  a:hover {
  background-color: var(--thm-base-hover);
}

.sigma_post .sigma_post-categories a + a {
  margin-left: 10px;
}

.sigma_post .sigma_post-body {
  padding: 30px;
  position: relative;
}

.sigma_post .sigma_post-body:before {
  content: "";
  position: absolute;
  bottom: -3px;
  right: -3px;
  width: 10px;
  height: 10px;
  background-color: var(--thm-base);
}

.sigma_post .sigma_post-meta {
  border-bottom: 1px solid var(--thm-border);
  padding-bottom: 15px;
}

.sigma_post .sigma_post-meta ul {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  margin-top: 15px;
}

.sigma_post .sigma_post-meta ul li {
  font-family: var(--thm-font);
  color: var(--thm-b-text);
  font-size: 14px;
  display: flex;
  align-items: center;
  position: relative;
  font-weight: 400;
  text-transform: uppercase;
}

.sigma_post .sigma_post-meta ul li i {
  margin-top: -4px;
}

.sigma_post .sigma_post-meta ul li a {
  color: var(--thm-b-text);
}

.sigma_post .sigma_post-meta ul li a:hover {
  color: var(--thm-base-hover);
}

.sigma_post .sigma_post-meta ul li + li {
  padding-left: 10px;
  margin-left: 10px;
}

.sigma_post .sigma_post-meta ul li + li:before {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 1px;
  height: 12px;
  background-color: var(--thm-border);
}

.sigma_post .sigma_post-date {
  position: absolute;
  right: 20px;
  top: -28px;
  text-align: center;
  padding: 12px 15px;
  font-weight: 600;
  background-color: var(--thm-base);
  color: #fff;
  line-height: 1;
  font-size: 15px;
  transition: 0.3s;
}

.sigma_post .sigma_post-date:hover {
  background-color: var(--thm-base-hover);
}

.sigma_post .sigma_post-body .sigma_post-content {
  padding: 15px 0 0;
}

.sigma_post .sigma_post-body .sigma_post-content h5 {
  margin-bottom: 15px;
}

.sigma_post .sigma_post-body .btn-link {
  color: var(--thm-secondary);
  font-weight: 500;
  margin-top: 15px;
  font-size: 14px;
}

.sigma_post .sigma_post-body .btn-link i {
  color: var(--thm-base);
}

.sigma_post .sigma_post-body .btn-link:hover {
  color: var(--thm-base-hover);
}

.sigma_post.style-1 .sigma_post-meta ul {
  margin-top: 0;
}

/* Style 2 */
.sigma_post.style-2 .sigma_post-body,
.sigma_post.style-10 .sigma_post-body,
.sigma_post.style-11 .sigma_post-body {
  position: absolute;
  bottom: 0;
  left: 0;
  padding: 25px;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  background: linear-gradient(
    180deg,
    rgba(0, 0, 0, 0) 0%,
    rgba(0, 0, 0, 0.6) 100%
  );
}

.sigma_post.style-2 img,
.sigma_post.style-10 img,
.sigma_post.style-11 img {
  width: 100%;
}

.sigma_post.style-2 .sigma_post-body:before,
.sigma_post.style-11 .sigma_post-body:before {
