  padding: 0;
  margin: 0 auto;
  outline: none;
  background-color: #f8f8f8;
  justify-content: center;
}

/* Tab items style 11 */
.sigma_tab-item.style-11 .nav-tabs {
  justify-content: start;
}

.sigma_tab-item.style-11 .nav-tabs .nav-link {
  flex-direction: column;
  border: 0;
  padding: 0;
  box-shadow: none;
}

