}

/* Tab items style 12 */
.sigma_tab-item.style-12 .nav-tabs {
  flex-direction: column;
}

.sigma_tab-item.style-12 .nav-tabs li {
  margin-bottom: 0;
}

.sigma_tab-item.style-12 .nav-tabs li + li {
  margin-left: 0;
  margin-top: 30px;
}

.sigma_tab-item.style-12 .nav-tabs .nav-link {
