}

/*-------------- Contact Form --------------------------- */
/* style 1 */
.sigma_form.has-shadow {
  position: relative;
  padding: 50px;
  border-radius: 6px;
  background-color: #fff;
  box-shadow: 0 0 40px 5px rgba(0, 0, 0, 0.03);
}

.sigma_form .sigma_form-info {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.sigma_form button.btn-block + button.btn-block {
  margin-top: 15px;
}

.sigma_form-seperator {
  margin: 20px 0;
  text-align: center;
  width: 100%;
}

.sigma_form-seperator span {
  position: relative;
  display: flex;
  align-items: center;
  font-size: 14px;
}

.sigma_form-seperator span:before {
  content: "";
  flex: 1;
  background-color: var(--thm-border);
  height: 1px;
  margin-right: 10px;
}

.sigma_form-seperator span:after {
  content: "";
  flex: 1;
  background-color: var(--thm-border);
  height: 1px;
  margin-left: 10px;
}

.sigma_contact-map {
  width: 100%;
  height: 600px;
  position: relative;
}

.sigma_contact-map iframe {
  border: 0;
  width: 100%;
  height: 100%;
}

.sigma_contact-wrapper .sigma_contact-blocks {
  padding: 50px 30px;
  border: 2px solid var(--thm-border);
  border-top: 0;
}

.sigma_contact-wrapper .sigma_contact-map {
  height: 350px;
}

/* contact block */

.sigma_contact-block + .sigma_contact-block {
  margin-top: 20px;
}

.sigma_contact-block p {
  margin-bottom: 5px;
}

/* contact block style 2 */

.sigma_contact-block.style-2 {
  display: flex;
  align-items: center;
}

.sigma_contact-block.style-2 .icon,
.sigma_contact-block.style-3 .icon {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  background-color: var(--thm-base);
  margin-right: 15px;
  font-size: 18px;
}

.sigma_contact-block.style-2 p {
  margin-bottom: 0;
}

/* contact block style 3 */

.sigma_contact-block.style-3 {
  display: flex;
  align-items: flex-start;
}

.sigma_contact-block.style-3 .icon {
  color: var(--thm-base);
  background-color: transparent;
  font-size: 30px;
  margin-right: 10px;
}

/* style 2 */
.sigma_form.style-2 .sigma_buttons button + button {
  margin-left: 15px;
}

/* style 3 */
.sigma_form.style-3 {
  max-width: 400px;
  margin: 0 auto;
}

.sigma_form-image {
  height: 100vh;
}

.sigma_form-image img {
  width: 100%;
  height: 100%;
}

.sigma_contact-map.has-full-height {
  height: 100%;
}

/* style 4 */
.sigma_form.style-4 {
  position: absolute;
  left: 50px;
  top: 50%;
  transform: translateY(-50%);
  z-index: 1;
}

.sigma_contact-with-bg {
  position: relative;
}
.sigma_contact-with-bg .absolute-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 33%;
  height: 100%;
  z-index: -1;
}

.sigma_contact.extra-padding {
  padding-bottom: 150px;
}
.sigma_contact-map.is-absolute {
  position: relative;
  margin-top: -150px;
  z-index: 1;
}

.sigma_contact-map.is-absolute iframe {
  background-color: #fff;
  padding: 10px;
  height: 400px;
  box-shadow: 0 0 10px 5px rgba(0, 0, 0, 0.03);
}

/* style 5 */
.sigma_form.style-5 {
  padding: 60px;
  box-shadow: 0 0 10px 5px rgba(0, 0, 0, 0.03);
  position: relative;
}

.sigma_form.style-5 .sigma_contact-map {
  height: 100%;
}

/* style 6 */

.sigma_contact-image.style-6 {
  position: absolute;
  left: 15px;
  top: -70px;
