.sigma_product {
  position: relative;
  margin-bottom: 30px;
}

.sigma_product .sigma_product-thumb {
  position: relative;
  overflow: hidden;
}

.sigma_product .sigma_product-thumb.thumb-sm {
  max-width: 200px;
  margin: 0 auto 20px;
}

.sigma_product .sigma_product-thumb img {
  transition: 0.3s;
  width: 100%;
}

.sigma_product .sigma_product-thumb:hover img {
  transform: scale(1.1) rotate(3deg);
}

.sigma_product .sigma_product-body {
  position: relative;
  padding: 15px 0 0;
}

.sigma_product .sigma_product-body h5 {
  margin: 0 0 10px;
  font-size: 22px;
  font-weight: 400;
  word-break: break-word;
}

.sigma_product .sigma_product-body .sigma_product-title a {
  color: var(--thm-secondary);
  font-weight: 600;
}

.sigma_product .sigma_product-body .sigma_product-title a:hover {
  color: var(--thm-base-hover);
}

.sigma_product .sigma_badge-featured {
  position: absolute;
  top: 10px;
  right: 10px;
  z-index: 3;
  background-color: #ffa80a;
  font-weight: 600;
  color: #fff;
  font-weight: 700;
  font-size: 16px;
  line-height: 1.7;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
}

.sigma_product .sigma_badge-featured i {
  display: inline-block;
}

.sigma_product .sigma_badge-sale {
  position: absolute;
  top: 10px;
  left: 10px;
  z-index: 3;
  background-color: var(--thm-secondary);
  font-weight: 600;
  color: #fff;
  font-weight: 700;
  font-size: 11px;
  line-height: 1.7;
  width: 60px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
}

.sigma_product .sigma_rating-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 10px;
}

.sigma_product .sigma_rating-wrapper span {
  font-weight: 600;
}

.sigma_product-price {
  display: flex;
  align-items: center;
}

.sigma_product-price span {
  display: block;
}

.sigma_product-price span:first-child {
  line-height: 1.2;
  color: var(--thm-base);
  font-weight: 700;
}

.sigma_product-price span + span {
  color: var(--thm-b-text);
  font-weight: 400;
  line-height: 1;
  margin-left: 10px;
  text-decoration: line-through;
  color: #d2d2d2;
}

.sigma_product-stock {
  font-weight: 600;
}

.sigma_product-stock.instock {
  color: #58da58;
}

.sigma_product-stock.outofstock {
  color: #f54978;
}

.sigma_product .sigma_rating {
  margin-bottom: 0;
}

.sigma_product-controls {
  display: flex;
  justify-content: center;
  flex-direction: column;
  position: absolute;
  top: 20px;
  right: 20px;
  opacity: 0;
  transition: 0.3s;
  visibility: hidden;
}

.sigma_product:hover .sigma_product-controls {
  opacity: 1;
  visibility: visible;
}

.sigma_product-controls a {
  width: 40px;
  height: 40px;
  background-color: #fff;
  color: var(--thm-b-text);
  border-radius: 50%;
}

.sigma_product-controls a + a {
  margin-top: 10px;
}

.sigma_product-controls a i {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}

.sigma_product-controls a:hover {
  background-color: var(--thm-base);
  color: #fff;
  box-shadow: 0 0 40px 5px rgba(0, 0, 0, 0.03);
}

.sigma_product-footer {
  display: flex;
  align-items: center;
}

/* Style 2 */
.sigma_product.style-2 .sigma_product-body p {
  margin-bottom: 15px;
}

.sigma_product.style-2 .sigma_product-body .sigma_product-price {
  position: absolute;
  top: 17px;
  right: 0;
}

.sigma_product.style-2 .sigma_product-body h5 {
  padding-right: 70px;
}

/* Style 3 */
.sigma_product.style-3 {
  position: relative;
  padding: 20px;
  background-color: #fff;
  border: 1px solid var(--thm-border);
  box-shadow: 0 0 40px 5px rgba(0, 0, 0, 0.03);
  border-radius: 8px;
  margin-bottom: 30px;
  transition: 0.3s;
}

.sigma_product.style-3 .sigma_rating {
  margin: 15px 0;
}

.sigma_product.style-3 .sigma_product-controls a {
  border-radius: 8px;
}

.sigma_product.style-4 .sigma_product-thumb:hover img,
.sigma_product.style-3 .sigma_product-thumb:hover img {
  transform: scale(1) rotate(0);
}

.sigma_product.style-3 .sigma_product-gallery {
  display: flex;
  position: absolute;
  bottom: 15px;
  left: 50%;
  transform: translateX(-50%);
  background-color: #fff;
  padding: 8px 5px;
  border-radius: 8px;
  width: 148px;
  transition: 0.3s;
  opacity: 0;
  visibility: hidden;
