.sigma_product.style-5,
.sigma_product.style-4 {
  text-align: center;
}

.sigma_product.style-5 .sigma_product-price,
.sigma_product.style-5 .sigma_rating,
.sigma_product.style-4 .sigma_product-price,
.sigma_product.style-4 .sigma_rating {
