/* Style 7 */
.sigma_product.style-7 {
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
  padding: 20px;
  background-color: #fff;
  box-shadow: 0 0 40px 5px rgba(0, 0, 0, 0.03);
