  width: 25px;
  height: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.sigma_product-radio label span::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(0);
  transition: 0.3s;
  opacity: 0;
  visibility: hidden;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  z-index: -1;
  background-color: var(--thm-base);
}

.sigma_product-radio label input:checked + span::before {
